import React from 'react';
import { BrowserRouter as Route, Switch } from 'react-router-dom'
import NavBar from './NavBar'
import '../styles/App.css'


function App(props) {
    const items = props.items;
    let renderRoutes = items.map((item, i) => {
        if (item.url === "/") {
            return (
                <Route exact path={item.url} component={item.component} key={`${item.url}`}>
                    < item.component />
                </Route>);
        } else {
            return (
                <Route path={item.url} component={item.component} key={`${item.url}${i}`}>
                    < item.component />
                </Route>);
        }
    });

    return (
        <div className="App">
            <header className="App-header">
                <NavBar items={items} activeItem={items[0].url} />
            </header>
            <Switch>
                {renderRoutes}
            </Switch>
        </div>
    );
}

export default App
