import React, {Component}from 'react';
import Input from './Input';
import '../styles/Search.css'

class Search extends Component {
    placeholder = ''
    _id = ''
    st=''
    constructor(props) {
        super();
        this.placeholder = props.placeholder;
        this._id = `_${props.placeholder}`;
    }
    handleCLick() {
        var _input = document.getElementById(this._id);
        _input.style.width = "auto";
        _input.focus();
        console.log(document.activeElement.classList)
    }
    render() {
        return (
            <div className="search-container float-right">
                <Input placeholder={this.placeholder} hide={true} id={this._id}></Input>
                <img src="https://img.icons8.com/ios/48/000000/search--v1.png"
                    alt="search-ico" id={`_${this.placeholder}#_`}
                    onClick={()=>this.handleCLick(this)}
                className="float-right"/>
            </div>

        );
    }
}
export default Search;