import React, { Component } from 'react';
import { BrowserRouter as Router, NavLink } from 'react-router-dom'
import '../styles/NavBar.css'
import logo from '../assets/logo.svg'
import Search from './Search'

let active = "NerdTronik";

class NavItem extends Component {
    url = ''
    name = ''
    title = ''
    id = ''
    constructor(props) {
        super();
        this.url = props.url;
        this.name = props.name;
        this.title = props.title;
        this.id = `${this.name}_${this.title}`;
    }
    activate() {
        let tabcontent = document.getElementsByClassName("nav-active");
        for (var i = 0; i < tabcontent.length; i++) {
            tabcontent[i].classList.remove("nav-active");
        }
        tabcontent = document.getElementsByClassName("nav-item");
        for (i = 0; i < tabcontent.length; i++) {
            if (tabcontent[i].id === this.id)
                tabcontent[i].classList.add("nav-active");
        }
    }
    render() {
        return (
            <NavLink to={this.url} className="nav-item" id={this.id} onClick={() => this.activate(this)}>
                {this.name}
            </NavLink>
        );
    }
}


function NavIcon(props) {
    const icon = props.src;
    const alt = props.alt;
    const id = `_${icon}_${alt}`
    return (
        <NavLink to='/' id={id}>
            <div>
                <img src={icon} alt={alt} className="nav-logo" />
            </div>
        </NavLink>
    )
}

class NavBar extends Component {
    static renderItems;
    constructor(props) {
        super();
        const items = props.items;
        this.renderItems = items.map(
            (item) =>
                <NavItem name={item.name} key={item.url} url={item.url} title={item.title} />
        );
    }
    render() {
        var prevScrollpos = window.pageYOffset;
        window.onscroll = () => {
            if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 50) {
                document.getElementById("NavBar").style.backgroundColor = "var(--background-color)";
                document.getElementById("NavBar").style.boxShadow = "0px 2px 8px -4px rgba(0,0,0,0.75)";
                document.getElementById("NavBar").style.height = "50px";
                document.getElementById("NavBar").style.fontSize = "calc(var(--font-size)*0.65)";
            } else {
                document.getElementById("NavBar").style.backgroundColor = "var(--background-color) 0%"
                document.getElementById("NavBar").style.boxShadow = "none"
                document.getElementById("NavBar").style.height = "100px";
                document.getElementById("NavBar").style.fontSize = "var(--font-size)";
            }
            var currentScrollPos = window.pageYOffset;
            if (prevScrollpos > currentScrollPos) {
                document.getElementById("NavBar").style.top = "0";
            } else {
                document.getElementById("NavBar").style.top = "-5em";
            }
            prevScrollpos = currentScrollPos;
        }

        return (
            <div className="nav" id="NavBar" onScroll={() => onscroll()}>
                <div className="nav-container w-30 Left">
                    <NavIcon src='../assets/logo.svg' alt="hi" />
                </div>
                <div className="nav-container w-40">
                    {this.renderItems}
                </div>
                <div className="nav-container w-30 right">
                    <Search placeholder="Search something..." />
                </div>
            </div>
        );
    }
}
export default NavBar;
