import React, { useState, useEffect } from 'react';
import { render } from 'react-dom';
import '../styles/Input.css'
function changeVisibility(visible) {

}
function Input(props) {
    const placeholder = props.placeholder;
    const id = props.id;
    const hide = props.hide;
    const hasIcon = props.hasIcon;
    var _class = '';
    if (hide !== undefined) {
        if (hide) { _class += "hide "; }
        else { _class += "show "; }
    }
    if (hasIcon !== undefined) {
        if (hasIcon) _class += "have-icon ";
    }
    const [visible, changeVisibility] = useState(hide === undefined ? true : !hide);
    useEffect(() => {
        if (hide) {
            var _input = document.getElementById(id);
            if (visible) {
                _input.classList.remove("hide")
                _input.classList.add("show")

            } else {
                _input.classList.remove("show")
                _input.classList.add("hide")
            }
        }
    })
    return (
        <div className="input-container">
            <input placeholder={placeholder}
                onBlur={() => changeVisibility(false)}
                onFocus={() => changeVisibility(true)}
                id={id}
                className={_class}
            ></input>
        </div>
    );
}

export default Input;